.questions {
  padding: 125px 0 108px;

  @media (max-width: $tablet-width) {
    padding: 74px 45px 80px;
  }

  @media (max-width: $mobile-width) {
    padding: 64px 15px 64px;
  }
}

.questions h2 {
  font-family: "Cormorant Garamond", Arial sans-serif;
  margin-top: 0;
  margin-bottom: 54px;
  text-align: center;
  font-size: 34px;
  line-height: 44px;

}

.questions h3 {
  margin: 0;
  font-size: 18px;
  line-height: 24px;
  cursor: pointer;
  font-weight: 500;
  @media (max-width: $mobile-width) {
    font-size: 16px;
    line-height: 22px;
  }

}

.questions__icon {
  display: block;
  width:17px;
  height: 10px;
  position: absolute;
  right: 50px;
  top: 50px;
  @media (max-width: $mobile-width) {
    right: 25px;
    top: 25px;
  }
}

.questions__icon--closed {
  background-image: url("../../img/icon-arrow-down.svg");
}

.questions__icon--opened {
  background-image: url("../../img/icon-arrow-up.svg");
}

.questions__list {
  list-style: none;
  padding: 0;
  border: 1px solid $brown;
  width: 970px;
  margin: 0 auto;

  @media (max-width: $tablet-width) {
    width: 100%;
  }
}

.questions__item {
  position: relative;
  border-bottom: 1px solid $brown;
  padding-left: 38px;
  padding-right: 400px;
  padding-top: 44px;
  padding-bottom: 44px;

  @media (max-width: $tablet-width) {
    padding-left: 50px;
    padding-right: 101px;
  }

  @media (max-width: $mobile-width) {
    padding: 20px 60px 20px 25px;
  }
}

.questions__answer {
  font-size: 16px;
  line-height: 21px;
  margin-bottom: 0;
  font-weight: 400;

  @media (max-width: $mobile-width) {
    font-size: 14px;
    line-height: 20px;
  }

}

.questions__answer--closed {
  display: none;
}
